	    var translateTransposeValue = function translateTransposeValue(val) {
	        var letter = '';
	
	        switch (val % 12) {
	            case 0:
	                letter += 'C ';
	                break;
	            case 1:
	                letter += 'C# ';
	                break;
	            case 2:
	                letter += 'D ';
	                break;
	            case 3:
	                letter += 'Eb ';
	                break;
	            case 4:
	                letter += 'E ';
	                break;
	            case 5:
	                letter += 'F ';
	                break;
	            case 6:
	                letter += 'F# ';
	                break;
	            case 7:
	                letter += 'G ';
	                break;
	            case 8:
	                letter += 'Ab ';
	                break;
	            case 9:
	                letter += 'A ';
	                break;
	            case 10:
	                letter += 'Bb ';
	                break;
	            case 11:
	                letter += 'B ';
	                break;
	            default:
	                console.log('impossible transpose value');
	        }
	        if (val < 12) {
	            letter += '1';
	        } else if (val < 24) {
	            letter += '2';
	        } else if (val < 36) {
	            letter += '3';
	        } else if (val < 48) {
	            letter += '4';
	        } else {
	            letter += '5';
	        }
	
	        return letter;
	    };
	
