	    var calculateBreakpointPitch = function calculateBreakpointPitch(val) {
	        var note = '';
	        switch (val % 12) {
	            case 0:
	                note += 'A ';
	                break;
	            case 1:
	                note += 'Bb ';
	                break;
	            case 2:
	                note += 'B ';
	                break;
	            case 3:
	                note += 'C ';
	                break;
	            case 4:
	                note += 'C# ';
	                break;
	            case 5:
	                note += 'D ';
	                break;
	            case 6:
	                note += 'Eb ';
	                break;
	            case 7:
	                note += 'E ';
	                break;
	            case 8:
	                note += 'F ';
	                break;
	            case 9:
	                note += 'F# ';
	                break;
	            case 10:
	                note += 'G ';
	                break;
	            case 11:
	                note += 'Ab ';
	                break;
	            default:
	                console.log('impossible note value');
	        }
	        if (val < 3) {
	            note += '-1';
	        } else if (val < 15) {
	            note += '0';
	        } else if (val < 27) {
	            note += '1';
	        } else if (val < 39) {
	            note += '2';
	        } else if (val < 51) {
	            note += '3';
	        } else if (val < 63) {
	            note += '4';
	        } else if (val < 75) {
	            note += '5';
	        } else if (val < 87) {
	            note += '6';
	        } else if (val < 99) {
	            note += '7';
	        } else {
	            note += '8';
	        }
	
	        return note;
	    };
	
